import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/HomeRounded';
// import AddRoundIcon from '@material-ui/icons/AddBoxRounded';
import ProfileIcon from '@material-ui/icons/PersonRounded';
import Fab from '@material-ui/core/Fab'
import { Link } from 'react-router-dom';
import AddIcon from '@material-ui/icons/Add';
const useStyles = makeStyles({
  root: {
    width: 500,
    bottom: 0
  },
  fabButton: {
    position: 'absolute',
    zIndex: 1,
    // top: -30,
    // left: 0,
    // right: 0,
    // margin: '0 auto',
  },
});

export default function Navigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  return (
    <div className="nav-botn">
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        showLabels
        className={classes.root}
      >
        <BottomNavigationAction value="home" component={Link} to="/" label="Home" icon={<HomeIcon />} />
        <Fab color="secondary" aria-label="add" className={classes.fabButton}>
          <BottomNavigationAction value="add" component={Link} label="" to={"/add"} icon={<AddIcon />} />

        </Fab>
        <BottomNavigationAction value="profile" component={Link} to="/profile" label="Profile" icon={<ProfileIcon />} />
      </BottomNavigation>
    </div>
  );
}
