import React from 'react';
import Navigation from '../Navigation/Navigation';
import { Box, Grid, Container, Typography, Card, CardActionArea, CardContent, CardMedia, Divider } from '@material-ui/core';
import Variants from './Variant';
import { makeStyles } from "@material-ui/core/styles";
import { SchoolRounded, Home, Person } from '@material-ui/icons'
import profileData from './profileData';
import AppBarred from '../AppBar/AppBarred';
const useStyles = makeStyles({
    root: {
        maxWidth: 345,
        margin: 10,
        borderRadius: 25
    },
    media: {
        height: 200,
    },
    title: {
        flexGrow: 1,
    }
});
export default function Profile() {
    const classes = useStyles();
    const [data, setdata] = React.useState([]);
    React.useEffect(() => {
        setdata(profileData);
    }, [])
    return (
        <Box>
            <Box >
                <AppBarred />
                <Box className="main-content">
                    <Container className="container">
                        <Grid
                            container
                            justify="center"
                            alignContent="center"
                            alignItems="center"
                            className="row">
                            {!data.length ? <Variants /> : (
                                data.map(data =>
                                    <Card key={data.name} className={classes.root}>
                                        <CardActionArea>
                                            <CardMedia
                                                className={classes.media}
                                                image={data.img}
                                                title={data.name}
                                            />
                                            <CardContent>
                                                <Typography style={{ textAlign: 'center' }} component="h4" variant="h4">
                                                    {data.name}
                                                </Typography>
                                                <Divider />
                                                <Typography component="small" variant="subtitle1" >
                                                    <SchoolRounded />{data.school}
                                                </Typography><br />
                                                <Typography component="small" varaint="subtitle1">
                                                    <Person /> {data.profession}
                                                </Typography><br />
                                                <Typography component="small" variant="subtitle1">
                                                    <Home />{data.address}
                                                </Typography>
                                                <Divider />
                                                <Typography style={{ textAlign: 'center' }} component="p" variant="caption">
                                                    {data.description}
                                                </Typography>
                                            </CardContent>
                                        </CardActionArea>
                                    </Card>
                                )
                            )}
                        </Grid>
                    </Container>
                </Box>
            </Box>
            <Navigation />
        </Box>

    )
}
